svg {
  position: relative;
  width: 150px;
  height: 150px;
}

svg circle {
  width: 100%;
  height: 100%;
  fill: none;
  stroke: #191919;
  stroke-width: 10;
  stroke-linecap: round;
  transform: translate(5px, 5px);
}

svg circle:nth-child(2) {
  stroke-dasharray: 283;
  stroke-dashoffset: 283;
}

.progess-card:nth-child(1) svg circle:nth-child(2) {
  stroke-dashoffset: calc(283 - (283 * 50) / 100);
  stroke-dashoffset: calc(283 - (283 * var(--rate, 50)) / 100);
  stroke: #f75a00;
}

.progess-card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  text-align: center;
  overflow: hidden;
  transition: 0.5s;
}
